@import "variables";
@import "../variables-2sic";

@media all {
	
	/*layout box*/
	body.va-layout-box {
		header {
			background: transparent;
			.container-fluid {
				background: $tosic-header-background;
			}

			&.fixed-header {
				box-shadow: none;
				.container-fluid {
					box-shadow: 0px 15px 20px -15px rgba(0,0,0,0.3);
				}
			}
		}
		.container-fluid {
			max-width:1400px;
			padding-left: $grid-gutter-width/2;
			padding-right: $grid-gutter-width/2;
			@include media-breakpoint-up(md) {
				padding-left: $grid-gutter-width*3;
				padding-right: $grid-gutter-width*3;
			}
		}
		.ly-header-pane {
			margin-top: 0;
			background: #fff;
			&.container-fluid {
				padding-left: 0;
				padding-right: 0;
			}
		}
		.ly-header-pane-empty {
			border-top: 5px solid theme-color("primary");
		}

		.ly-fullwrapper .ly-push { display: none; }
	}
}