@import "variables";
@import "../variables-2sic";

@media all {
	.role-admin header {
		margin-top: 54px;
	}
	header { 
		background: $tosic-header-background;
		width: 100%;
		position: relative;
		z-index: 800;
		&.fixed-header {
			box-shadow: 0px 2px 16px 0px rgba(0,0,0,0.3);
			position:fixed;
			z-index: 20;
			top: -150px;
			transition: top .4s ease-in-out;
			&.fixed-top {
				top:0;
			}
			.container-fluid { 
				padding-top: $grid-gutter-width/2;
				padding-bottom: $grid-gutter-width/2;
			}
			.ly-logo {
				width: 120px;
				svg {
					/* If you use an SVG as Logo, you will have to give it a fixed height
					   in this selector, orelse you may experience issues in IE */
					height: 37px;
				}
			}
		}
		
		.container-fluid { 
			padding-top: $grid-gutter-width/2;
			padding-bottom: $grid-gutter-width/2;
			@include media-breakpoint-up(md) {
				padding-top: $grid-gutter-width/1.5;
				padding-bottom: $grid-gutter-width/1.5;
			}
		}
	} 
	
    .ly-logo {
		display: block;
		width:120px;
		max-width:80%;
		height: auto;
		transition: width 0.4s ease-in-out;
		@include media-breakpoint-up(md) {
			width:170px;
		}

        img {
            width:100%;
            max-width:100%;
			height: auto;
		}
		
		svg {
			/* If you use an SVG as Logo, you will have to give it a fixed height
			   in this selector, orelse you may experience issues in IE */
			   height: 37px;
			   @include media-breakpoint-up(md) {
				height: 53px;
			}
		}
    }

    .ly-header-pane { 
		padding:0;
	}
}